import React from 'react';

class AudioPlayer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isPlaying: true
    };
  }

  handlePlay = () => {
    if (this.isPlaying) {
      this.setState({
        isPlaying: false
      });
    } else {
      this.setState({
        isPlaying: true
      });
    }
    
  }
  
  render() {
    let imgSource = this.isPlaying ? "../../../static/images/pause.svg" : "../../../static/images/play.svg";
    return (
      <div>
          <button onClick={this.handlePlay} ><img src={imgSource} className="playButton"/></button>
      </div>
    );
  }
}

export default AudioPlayer;